.navbar {
  position: sticky;
  top: 0px;
  height: 70px;
  background-color: black;
  opacity: 0.75;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 30px 0px 30px;
  margin-bottom: -70px;
  z-index: 5;
}

.navbarLogo {
  color: white;
  font-size: 2.5rem;
}

.navbarList {
  display: flex;
  list-style: none;
  background-color: black;
  opacity: 0.75;
}

.navbarItem {
  white-space: nowrap;
}

.navbarLinks {
  font-size: 0.85rem;
  text-decoration: none;
  color: white;
  text-align: center;
  padding: 10px;
  margin: 0px 5px;
  border-style: solid;
  border-width: 0px 0px 1px 0px;
  border-color: transparent;
  transition: 250ms;
}

.navbarLinks:hover {
  border-color: white;
  transition: 250ms;
}

.navbarMenu {
  color: white;
  cursor: pointer;
  display: none;
}

@media screen and (max-width: 800px) {
  .navbarList {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    position: absolute;
    top: 50px;
    left: -100%;
    width: 100%;
    height: 300px;
    transition: all 0.5s ease;
  }

  .navbarList--active {
    left: 0;
  }

  .navbarItem {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0;
    flex: 1;
  }
  .navbarLinks {
    display: flex;
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
    margin: 0px;
    border-width: 0px;
    border-color: black;
    opacity: 0.75;
  }
  .navbarLinks:hover {
    opacity: 0.9;
    border-width: 0px;
  }
  .navbarMenu {
    display: block;
  }
}
